"use client";

import { Modal, ModalOverlay, ModalContent, ModalHeader, ModalBody, ModalCloseButton, Box, Avatar, Text, Button, useDisclosure } from "@chakra-ui/react";
import { cn } from "@/lib/utils"; // ShadCN utility function for conditional classes

const MainProfile = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <>
      {/* Trigger Button */}
      <Button onClick={onOpen} colorScheme="teal">
        Open Profile
      </Button>

      {/* Modal with Blur Background */}
      <Modal isOpen={isOpen} onClose={onClose} size="lg" isCentered>
        <ModalOverlay 
          bg="blackAlpha.600" 
          backdropFilter="blur(10px)" // Blur background
        />
        <ModalContent
          className={cn("rounded-lg shadow-xl bg-white/80 backdrop-blur-lg")}
        >
          <ModalHeader>User Profile</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <Box textAlign="center" p={4}>
              <Avatar
                size="xl"
                name="User Name"
                src="/path-to-avatar.jpg"
                mb={4}
              />
              <Text fontSize="lg" fontWeight="bold">John Doe</Text>
              <Text fontSize="sm" color="gray.500">johndoe@example.com</Text>
              <Button mt={4} colorScheme="blue">Edit Profile</Button>
            </Box>
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
};

export default MainProfile;
